import React, { PureComponent } from 'react';
import createContext from 'create-react-context';
const defaultLocale = {};
export const LocaleContext = createContext(defaultLocale);
let runTimeLocale;

const changeRunTimeLocale = locale => {
  runTimeLocale = locale;
};

export const getRunTimeLocale = () => runTimeLocale;
export default class LocaleProvider extends PureComponent {
  render() {
    const {
      children,
      locale
    } = this.props;
    changeRunTimeLocale(locale);
    return React.createElement(LocaleContext.Provider, {
      value: locale
    }, React.Children.only(children));
  }

}
LocaleProvider.defaultProps = {
  locale: {}
};